import React from 'react';
import {StyleSheet, Text, View, Platform} from 'react-native';
import {ChunkManager} from '@callstack/repack/client';

ChunkManager.configure({
  // storage: AsyncStorage,
  resolveRemoteChunk: async (chunkId, parentId) => {
    console.log('*****CHUNKID', chunkId, parentId);
    return {url: 'http://localhost:8082/sampleRepackTeacher.chunk.bundle'};
  },
});

async function loadComponent(scope, module) {
  // Initializes the share scope. This fills it with known provided modules from this build and all remotes
  await __webpack_init_sharing__('default');
  // Download and execute container
  await ChunkManager.loadChunk(scope, 'main');

  const container = self[scope];

  // Initialize the container, it may provide shared modules
  await container.init(__webpack_share_scopes__.default);
  const factory = await container.get(module);
  const exports = factory();
  return exports;
}

const SampleRepackTeacher = React.lazy(
  async () => await loadComponent('sampleRepackTeacher', './H.js'),
);

function AppWrapper() {
  return (
    <React.Suspense
      fallback={<Text style={{textAlign: 'center'}}>Loading...</Text>}>
      <SampleRepackTeacher />
      <Text>hello</Text>
    </React.Suspense>
  );
}

const CodeSplit = () => {
  return (
    <View>
      <Text>sampleRepackTeacher</Text>
      <AppWrapper />
    </View>
  );
};

export default CodeSplit;

const styles = StyleSheet.create({});
